import React from "react";
import Modal from 'react-bootstrap/Modal';
import { AiFillStar, AiOutlineHeart, AiOutlinePlus, AiOutlineMinus } from 'react-icons/ai';
import { dollar } from "./static/constant";
const ProducstDetails = ({ handleClose ,productData, productRaings, open, addToCart, addWishlist }) => {
  return(
    <>
      <Modal show={open} onHide={handleClose} size="lg">
        <Modal.Header closeButton />
        <Modal.Body>
          <div className="row">
            <div className="col-md-6">
            <div id="carouselExampleIndicators" class="carousel slide" data-bs-ride="carousel">
              <div class="carousel-inner">
                <div class="carousel-item active">
                  <img src={productData.image} class="d-block w-100" alt="..." />
                </div>
              </div>
            </div>
            </div>
            <div className="col-md-6">
              <h2 className="fw-bold">{productData.title}</h2>
              <h5 className="text-secondary">&#8377;&nbsp;{parseInt(productData.price) * dollar}</h5>
              {
                productRaings.map((rating, i) => {
                  return(
                    <>
                      <AiFillStar key={i} fontSize={'18px'} color="yellow" />
                    </>
                  )
                })
              }&nbsp;&nbsp;&nbsp;( { Math.round(productData.rating.rate) }&nbsp;Reviews )
              <p className="m-0 py-2">{productData.description}</p>
              <div class="row my-3">
                <div className="col-md-12">
                  <button type="button" className="btn btn-md btn-custom-primary" onClick={ () => addToCart(productData)}>Add To Cart</button>&nbsp;
                  <button type="button" className="btn btn-md btn-outline-custom-primary" onClick={ () => addWishlist(productData)}><AiOutlineHeart />&nbsp;Add to Wishlist</button>
                </div>
              </div>
              <div className="row">
                <div className="col-md-12">
                  <span className="fw-bold">Social Share : </span>
                </div>
              </div>
            </div>
          </div>
        </Modal.Body>
        <Modal.Footer>
          {/* <Button variant="secondary" onClick={handleClose}>
            Close
          </Button>
          <Button variant="primary" onClick={handleClose}>
            Save Changes
          </Button> */}
        </Modal.Footer>
      </Modal>
    </>
  )
}

export default ProducstDetails;
