// import { createSlice } from "@reduxjs/toolkit";
import { combineReducers, createSlice } from "@reduxjs/toolkit";

const cartProductSlice = createSlice({
  name: 'cart',
  initialState: [],
  reducers: {
    addProduct(state, action) {
      state.push(action.payload)
    },
    removeProduct(state, action) {
      state.splice(action.payload, 1)
    }
  }
});

const wishListSlice = createSlice({
  name: 'wishList',
  initialState: [],
  reducers: {
    saveWishlist(state, action) {
      console.log(action.payload)
      state.push(action.payload)
    }
  }
})

// export default cartProductSlice.reducer;
export default combineReducers({
  cartProductSlice: cartProductSlice.reducer,
  wishListSlice: wishListSlice.reducer
});
export const { addProduct, removeProduct } = cartProductSlice.actions;
export const { saveWishlist } = wishListSlice.actions;